.form {
  margin-top: 20px;
  margin-bottom: 20px;
}
.item {
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
}
.error {
  margin-top: 4px;
  color: red;
  font-size: 14px;
}
.label {
  color: lightgrey;
  margin-bottom: 5px;
}
.input {
  width: 300px;
  height: 40px;
  border: 2px solid black;
  outline: none;
  padding-left: 10px;
  transition: 0.2s;
  color: black;
}
.item:hover .input,
.input:focus,
.input:active {
  color: rgb(190, 123, 0);
  border: 2px solid rgb(190, 123, 0);
}
.btn {
  width: 315px;
  height: 40px;
  border: none;
  font-size: 17px;
  font-weight: 600;
  background-color: rgb(190, 123, 0);
}
